.upload-cell-container {
  padding-bottom: 0.37333rem;
}
.upload-container {
  height: 2.82667rem;
  padding-right: 0.24rem;
}
.upload-container:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.upload-preview {
  float: left;
  width: 4.53333rem;
  height: 2.82667rem;
  overflow: hidden;
}
.upload-pic {
  float: right;
  width: 4.6rem;
  height: 2.82667rem;
  overflow: hidden;
}
.upload-preview-img,
.upload-pic-img {
  width: 100%;
  display: block;
}



